.logo {
  display: flex;
  align-items: center;
  font-size: 32px;
  font-weight: bold;
  color: #000;
  padding: 10px;
  font-family: "Jolly Lodger", cursive;
}

.logo img {
  width: 50px;
  margin-right: 10px;
}

.button {
  box-sizing: border-box;
  padding: 14px;
  border: 2px solid;
  outline: none;
  background-color: #f1f3f5;
  cursor: pointer;
  transition: opacity 0.3s ease;
  display: inline-block;
  font-size: 14px;
}

.button:hover {
  opacity: 0.8;
}

.button:active {
  opacity: 0.6;
}

.button + .button {
  margin-left: 1rem;
}

.button-primary {
  background-color: #000;
  color: var(--base-color);
}

.button-primary:hover {
  color: #000;
  background-color: var(--base-color);
}

.button-secondary {
  background-color: #000;
  color: var(--base-color);
}

.button--disabled {
  cursor: not-allowed;
  opacity: 0.6;
}

.loader-wrapper {
    display: flex;
    justify-content: center;
    align-items: center;
}

.loader {
    border-radius: 50%;
    border: 2px solid;
    border-left-color: transparent;
    display: inline-block;
    -webkit-animation: spin 1s infinite;
            animation: spin 1s infinite;

    height: 20px;
    width: 20px;
}

.loader--sm {
    height: 10px;
    width: 10px;
}

.loader--lg {
    height: 40px;
    width: 40px;
}

@-webkit-keyframes spin {
    0% {
        -webkit-transform: rotate(0deg);
                transform: rotate(0deg);
    }

    100% {
        -webkit-transform: rotate(359deg);
                transform: rotate(359deg);
    }
}

@keyframes spin {
    0% {
        -webkit-transform: rotate(0deg);
                transform: rotate(0deg);
    }

    100% {
        -webkit-transform: rotate(359deg);
                transform: rotate(359deg);
    }
}

.connect-wallet {
    display: flex;
    align-items: center;
}

.connect-wallet__account {
    padding: 10px;
    display: inline-block;
    padding: 1rem;
    border-radius: 12px;
    outline: none;
    background-color: var(--base-color);
    border-radius: 0;
    border: 0;
    margin-right: 20px;
    font-size: 14px;
}

.connect-wallet__disconnect-button {
    background-color: transparent;
    border-color: transparent;
}

.connect-wallet__modal .modal-title {
    color: var(--base-color);
    margin-bottom: 20px;
}

.connect-wallet__modal .modal-dialog {
    position: relative;
}

.connect-wallet__modal .modal-content {
    background: #000;
    color: #fff;
    border: 10px solid var(--base-color);
    border-radius: 0;
    padding: 20px;
    z-index: 0;
}

.connect-wallet__modal .modal-dialog::after {
    content: "";
    position: absolute;
    width: 100%;
    left: 0;
    bottom: -90px;
    height: 110px;
    background-image: url('/images/honey-smudges.svg');
    background-repeat: repeat-x;
    z-index: -1;
}

.connect-wallet__option {
    display: flex;
    align-items: center;
    padding: 10px;
    border: 4px solid var(--base-color);
    grid-gap: 10px;
    gap: 10px;
}

.connect-wallet__option:hover {
    cursor: pointer;
    color: var(--base-color);
}

.connect-wallet__option + .connect-wallet__option {
    margin-top: 10px;
}

.connect-wallet__option-icon {
    width: 35px;
}

.connect-wallet__option-icon img {
    width: 100%;
    height: auto;
}
.container {
    padding: 0 15px;
    max-width: 1280px;
    margin: 0 auto;
    width: 100%;
}
.header {
  padding: 10px 0;
  width: 100%;
  margin-bottom: 70px;
  background-color: var(--base-color);
  position: relative;
}

.header::before {
  content: "";
  position: absolute;
  width: 100%;
  bottom: -110px;
  height: 110px;
  background-image: url("/images/honey-smudges.svg");
  background-repeat: repeat-x;
  z-index: -1;
}

.header__content {
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: space-between;
  flex-wrap: wrap;
  position: relative;
  z-index: 1;
}

.header__content-buttons {
  display: flex;
  grid-gap: 10px;
  gap: 10px;
  flex-wrap: wrap;
}

.audio-button {
  background: none;
  border: none;
}

.app {
  display: flex;
  flex-direction: column;
  align-items: center;
  height: 100vh;
}

.app-main-layout {
  display: flex;
  flex-wrap: wrap;
  grid-gap: 30px;
  gap: 30px;
}

.text-error {
  color: #ff6b6b;
}

.social-icon {
  width: 40px;
  height: 40px;
  background-color: #f1f3f5;
  border-radius: 50%;
  overflow: hidden;
  border: 2px solid var(--base-color);
  box-sizing: border-box;
}

.social-icon svg {
  width: 100%;
  height: 100%;
}

.social-icon--telegram {
  color: #29b6f6;
}

.social-icon--twitter {
  background-color: #29b6f6;
  color: #fff;
  padding: 7px;
}

.social-icon--discord {
  padding: 4px;
  color: #fff;
  background-color: #5865f2;
}

.social-icon--docs {
  padding: 7px;
  color: #fff;
  background-color: #000;
}

.social-icon--audit {
  padding: 7px;
  background-color: #000;
}

.social-icon--youtube {
  padding: 5px;
  color: red;
  background-color: #fff;
}

.socials {
    display: flex;
    grid-gap: 10px;
    gap: 10px;
    margin-bottom: 20px;
}

.social {

}

.social:hover {
    cursor: pointer;
}
.footer {
    color: #fff;
    width: 100%;
}


.footer__copyright {
    padding: 10px 0 20px;
    text-align: center;
}

.footer__socials {
    text-align: center;
    padding: 20px 0 10px;
    display: flex;
    justify-content: center;
}

.footer__honeypots {
    margin-top: 20px;
    margin-bottom: 20px;
    width: 100%;
    height: 140px;
    background-image: url('/images/honeypot.svg');
    background-position: center;
    background-repeat: repeat-x;
    background-size: 100px 120px;
}
.hive-card {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  width: 100%;
  height: 100%;
  color: #fff;
  background: #000;
  position: relative;
  margin-bottom: 40px;
}

.hive-card__content {
  padding: 35px;
  position: relative;
  z-index: 1;
}

.hive-card::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  border: 10px solid var(--base-color);
  z-index: 0;
}

.hive-card::after {
  content: "";
  position: absolute;
  width: 100%;
  left: 0;
  bottom: -90px;
  height: 110px;
  background-image: url("/images/honey-smudges.svg");
  background-repeat: repeat-x;
  z-index: -1;
}

.hive-card__title {
  font-size: 34px;
  margin-bottom: 30px;
  color: var(--base-color);
  font-family: "Jolly Lodger", cursive;
}

.section {
    margin: 10px 0;
    width: 100%;
}

.token-countdown__title {
}

.token-countdown__timer {
  font-size: 60px;
  font-family: "Jolly Lodger", cursive;
}

*, *::before, *::after {
  box-sizing: border-box;
}

:root {
  --base-color: #F7B238;
  --base-fade-color: #BD8700;
  --text-color: #fff;
  --background-color: #000;
}

body, html {
  margin: 0;
  padding: 0;
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: #000;
  background: var(--background-color);
  background-image: url("/images/pattern.svg");
  background-size: 150px 150px;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}


.text-center {
  text-align: center;
}
